<template>
  <el-card style="margin:0 auto; width:1200px;font-family: 微软雅黑">
    <div>
      <el-form :model="dataForm" status-icon :rules="rules" ref="dataForm">
        <el-row :gutter="10" v-for="(item,index) in dataList" :key="index">
          <el-col :span="1">
          </el-col>
          <el-col :span="4" style="line-height: 18px">
            <el-col :span="22">{{item.username}}</el-col>
            <el-col :span="22">{{item.gmtCreate}}</el-col>
          </el-col>
          <el-col :span="1.5" @click.native="commentHandle (index)" style="padding-top: 15px;cursor:pointer;">
            <span style="color:#bab7b7;font-size: 14px">反馈</span>
          </el-col>
          <el-col :span="24">
            <div
                style="border-left: #c7c4c4 solid 3px;margin-left: 6px;margin-top: 8px;padding-left: 15px;font-size:16px ">
              <div style="border-bottom: #c7c4c4 dashed  1px;padding: 10px 10px" v-for="(item1,index) in item.children">
                <span style="color: #3e9cfe">{{item1.username}}</span>：<span>{{item1.content}}</span>
                <div style="padding-top: 10px;font-size: 13px;color:#bab7b7 ">{{item1.gmtCreate}}</div>
              </div>
              <div style="padding:10px 0px">
              <span style="color: #bab7b7;cursor:pointer;" @click="commentHandle (index)">添加新反馈</span>
              </div>
              <div style="padding:10px 0px" v-if="choice === index">
                <el-form-item prop="comment">
                  <el-input
                      v-model="dataForm.comment"
                      type="textarea"
                      style="width: 80%"
                      :rows="4"
                      maxlength="255"
                      show-word-limit
                      placeholder="请输入内容">
                  </el-input>
                </el-form-item>
                <div style="width: 80%;text-align: right;margin-top: 10px">
                  <el-button @click="commentBack">取消</el-button>
                  <el-button type="success" @click="comment (item.parent.id)">发送</el-button>
                </div>
              </div>
            </div>
          </el-col>
          <el-col :span="24">
            <el-divider></el-divider>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <!-- 分页 -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getAllComment"
    />
  </el-card>
</template>

<script>

export default {
  data () {
    return {
      dataForm: {
        comment: ''
      },
      choice: -1,
      title: '',
      pid: 0,
      type: 0,
      dataList: [],
      page: 1,
      limit: 10,
      total: 0,
      dataListLoading: false,
      dataListSelections: [],
      addOrUpdateVisible: false,
      rules: {
        comment: [{required: true, message: '评论不能为空', trigger: 'blur'},
          {min: 1, max: 255, message: '长度在 1 到 255 个字符', trigger: 'blur'}]
      }
    }
  },
  created() {
    this.getAllComment()
  },
  methods: {
    getAllComment(page = 1){
      this.page = page
      this.getRequest(`/tora/comment/getCommentByBlogId/24/${this.page}/${this.limit}`).then(res=>{
        this.dataList = res.data.records
        this.total = res.data.total
      })
    },
    //评论框弹出
    commentHandle (index) {
      if (index !== this.choice) {
        this.$refs['dataForm'].resetFields()
        this.choice = index
      }
    },
    //评论框取消取消
    commentBack () {
      this.$refs['dataForm'].resetFields()
      this.choice = -1
    },
    //评论
    comment (id) {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(`/pro/proevent/save`),
            method: 'post',
            data: this.$http.adornData({
              'title': this.dataForm.comment,
              'pid': this.pid,
              'typeId': id,
              'type': this.type
            })
          }).then(({data}) => {
            if (data && data.code === 0) {
              this.$message({
                message: '操作成功',
                type: 'success',
                duration: 500,
                onClose: () => {
                  this.choice = -1
                  this.getDataList(this.pid, this.title, this.type)
                  this.$refs['dataForm'].resetFields()
                }
              })
            } else {
              this.$message.error(data.msg)
            }
          })
        }
      })
    }
  }
}
</script>
<style scoped>
.el-divider {
  height: 0.1px;
}

.el-tab-pane /deep/ .el-card__body {
  background-color: #0b5b76 !important;
}
</style>