<template>
  <div>
    <h1>编辑</h1>
    <el-form ref="form" :model="blogForm" label-width="80px">
      <el-form-item>
        <el-input placeholder="请输入标题" v-model="blogForm.title" class="input-with-select" style="width: 30%">
          <el-select v-model="blogForm.type" slot="prepend" placeholder="请选择" style="width: 100px">
            <el-option label="原创" value="原创"></el-option>
            <el-option label="翻译" value="翻译"></el-option>
            <el-option label="转载" value="转载"></el-option>
          </el-select>
        </el-input>
      </el-form-item>

      <el-form-item label="分类">
        <el-select v-model="blogForm.classify" placeholder="请选择分类">
          <el-option v-for="classify in classifyList"  :label="classify.name" :value="classify.name"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标签">
        <el-select
            v-model="blogForm.tagIds"
            multiple
            collapse-tags
            placeholder="请选择">
          <el-option
              v-for="tag in tagList"
              :key="tag.id"
              :label="tag.name"
              :value="tag.id">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-checkbox v-model="blogForm.trecommend" label="推荐" border></el-checkbox>
        <el-checkbox v-model="blogForm.tcomment" label="开启评论" border></el-checkbox>
      </el-form-item>

      <el-form-item label="内容">
        <el-input type="textarea" :rows="12" v-model="blogForm.content"></el-input>
      </el-form-item>

<!--      <el-form-item label="封面">-->
<!--        <el-upload-->
<!--            class="upload-demo"-->
<!--            action="http://localhost:8080/oss/file/upload"-->
<!--            :on-success="handleSuccess"-->
<!--            :before-upload="beforeUpload"-->
<!--            :on-remove="handleRemove"-->
<!--            :file="file"-->
<!--            list-type="picture">-->
<!--          <el-button size="small" type="primary">点击上传</el-button>-->
<!--          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2MB</div>-->
<!--        </el-upload>-->
<!--      </el-form-item>-->

      <el-form-item label="封面">
        <el-upload
            class="avatar-uploader"
            action="http://localhost:8080/oss/file/upload"
            :show-file-list="false"
            :on-success="handleSuccess"
            :before-upload="beforeUpload"
        >
          <img v-if="blogForm.cover" :src="blogForm.cover" class="avatar"  />
          <i v-else class="el-icon-plus avatar-uploader-icon"
             style="border: 1px solid"></i>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2MB</div>
        </el-upload>
      </el-form-item>


      <el-form-item>
        <el-button type="primary" @click="saveOrUpdate">发布</el-button>
        <el-button @click="draft">保存</el-button>
        <el-button @click="back" v-if="blogForm.id">返回</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
export default {
  name: "save",
  data() {
    return {
      blogForm: {
        title: "",
        type: "",
        username: "",
        classify: "",
        content: "",
        tpublish:true
      },
      classifyList:{
        name:""
      },
      tagList:[],
      file:[],
      fileName:""
    }
  },
  created() {
    this.getAllClassify();
    this.init();
    this.getAllTag();
  },
  methods: {
    //删除图片
    handleRemove(){
      this.postRequest(`/oss/file/delete/${this.fileName}`).then(res=>{
        this.blogForm.cover = "";
        this.fileName = ""
      })
    },

    //上传图片处理
    handleSuccess(res, file) {
      this.blogForm.cover = res.data.url;
      console.log(this.blogForm.cover)
    },
    beforeUpload(file) {
      const isIMG =
          file.type === "image/jpg" ||
          file.type === "image/jpeg" ||
          file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isIMG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isIMG && isLt2M;
    },

    //点击保存
    draft(){
      this.blogForm.tpublish=false;
      if (!this.blogForm.id){
        this.addBlog()
      } else {
        this.updateBlogById()
      }
    },

    back(){
      this.$router.replace(`/blog`)
    },

    init(){
      //判断路径有id值,做修改
      if (this.$route.params && this.$route.params.id) {
        //从路径获取id值
        const id = this.$route.params.id;
        //调用根据id查询的方法
        this.getRequest(`/tora/blog/getByBlogId/${id}`).then(response=>{
          this.blogForm = response.data.blog;
          this.fileName = this.blogForm.cover.substring(53)
        })
      } else {
        //路径没有id值，做添加
        //清空表单
        this.blogForm = {
          title: "",
          type: "",
          username: "",
          classify: "",
          content: ""
        }
      }
    },

    //查询所有分类
    getAllClassify(){
      this.getRequest(`/tora/classify/getAllClassify`).then(response=>{
        this.classifyList = response.data.classifyList
      })
    },

    //查询所有标签
    getAllTag(){
      this.getRequest(`/tora/tag/getAllTag`).then(res=>{
        this.tagList = res.data.tagList
      })
    },

    //发布博客
    addBlog(){
      this.postRequest(`/tora/blog/addBlog`,this.blogForm).then(response=>{
        this.$confirm("是否继续?", "提示", {
          confirmButtonText: "继续",
          cancelButtonText: "返回",
          type: "warning"
        })
            .then(() => {
              this.$router.push({ path: "/blogSave" });
              this.blogForm = {
                title: "",
                type: "",
                username: "",
                classify: "",
                content: "",
                trecommend:0,
                tcomment:0
              };
            })
            .catch(() => {
              this.$router.replace({ path: "/blog" });
            });
      })
    },
    //编辑博客
    updateBlogById(){
      this.postRequest(`/tora/blog/updateBlogById`,this.blogForm).then(response=>{
        this.$router.replace({ path: "/blog" });
      })
    },


    saveOrUpdate() {
      this.blogForm.tpublish=true;
      if (!this.blogForm.id){
        this.addBlog()
      } else {
        this.updateBlogById()
      }
    }
  }
}
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>