<template>
  <div>
    <h2>分类管理</h2>
    <el-button type="danger" class="el-button--mini" @click="removeBatch">批量删除</el-button>
    <el-table
        :data="classifyList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        highlight-current-row
        style="width: 100%"
        ref="selection"
        @cell-dblclick="cellDoubleClick"
        @cell-click="cellClick"
        @selection-change="selectionChange">
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column
          sortable
          label="日期"
          prop="gmtCreate"
          width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.gmtCreate }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="分类"
          prop="name"
          width="180">
        <template slot-scope="scope">
<!--          @change="handleEdit(scope.$index,scope.row)"-->
          <el-input size="medium"
                    v-model="scope.row.name"
                    v-if="scope.row.seen"
                    placeholder="请输入内容"
                    ref="name"
                    @keyup.enter.native="addRow"
                    @blur="loseFcous(scope.$index, scope.row)"
          ></el-input>
          <el-tag size="medium" ref="name" v-else>{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column>
        <template slot="header" slot-scope="scope">
          <input
              class="search"
              v-model="search"
              size="medium"
              placeholder="输入关键字搜索"/>
        </template>
        <template slot-scope="scope">
          <el-button
              v-if="!scope.row.id"
              size="mini"
              type="success"
              @click="handleSave(scope.$index, scope.row)">
            保存
          </el-button>
<!--          <el-button-->
<!--              v-if="!scope.row.id"-->
<!--              size="mini"-->
<!--              @click="removeRow(scope.$index, scope.row)">-->
<!--            取消-->
<!--          </el-button>-->
          <el-button
              size="mini"
              type="danger"
              v-if="scope.row.id"
              @click="handleDelete(scope.$index, scope.row)">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-col :span=24>
      <div class="el-table-add-row" style="width: 99.2%;" @click="addRow()"><span>+ 添加</span></div>
    </el-col>
    <br>
    <br>
    <!-- 分页 -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getAllClassify"
    />

  </div>
</template>


<script>

export default {
  name: "classify",
  data(){
    return{
      classifyList:[],
      search:"",
      selections:[],
      page:1,
      total:0,
      limit:10
    }
  },
  created() {
    this.getAllClassify()
  },
  methods:{
    keyup(){

    },
    removeRow(row){
      this.classifyList.pop(row)
    },
    handleSave(row){
      this.postRequest(`/tora/classify/addClassify`,row).then(res=> {
        // this.getAllClassify()
      })
    },
    removeBatch(){
      this.$confirm("确定要批量删除选中的记录?","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/classify/removeBatch`,this.selections).then(res=>{
          this.getAllClassify()
        })
      })
    },
    //添加新行
    addRow(){
      let row = {name:"",seen:true}
      this.classifyList.push(row)
      console.log(row)
      this.$nextTick(()=>{
        this.$refs.name.focus()
      })
    },
    cellClick(row){
      this.$refs.selection.toggleRowSelection(row)
    },
    selectionChange(row){
      this.selections = row
      console.log(this.selections)
    },
    //查询所有分类
    getAllClassify(page =1 ){
      this.page = page;
      this.getRequest(`/tora/classify/classifyPage/${this.page}/${this.limit}`).then(response=>{
        this.classifyList = response.data.records
        this.total = response.data.total
      })
    },
    handleEdit(index, row) {
      if (row.id){
        this.postRequest(`/tora/classify/updateClassify`,row).then(res=>{
          this.$message.success({message:"修改成功"})
        })
      }else {
        this.handleSave(row)
      }

    },
    handleDelete(index, row) {
      if (row.id){
        this.$confirm("是否删除该记录？","提示",{
          confirmButtonText:"删除",
          cancelButtonText:"取消"
        }).then(()=>{
          this.deleteRequest(`/tora/classify/deleteClassifyById/${row.id}`).then(res=>{
            this.$message.success({message:"删除成功"})
            this.getAllClassify()
          })
        })
      }else{
        this.classifyList.pop(row)
      }
    },
    loseFcous(index, row) {
      row.seen=false;
      if (!row.id&&!row.name){
        this.classifyList.pop(row)
      }else if (!row.id&&row.name){
        this.handleSave(row)
      }else{
        this.handleEdit(index,row)
      }
    },
    cellDoubleClick(row, column) {
      row.seen=true;
      this.$nextTick(()=>{
        this.$refs.name.focus()
      })
    }
  }
}
</script>

<style scoped>
.tb-edit .el-input {
  display: none
}
.tb-edit .current-row .el-input {
  display: block
}
.tb-edit .current-row .el-input+span {
  display: none
}
.search{
  border-radius: 2px;
  padding: 10px 8px 10px 8px;
  border: 0 none;
  border-bottom: 1px solid #ccc;
}
.el-table-add-row {
  margin-top: 10px;
  width: 100%;
  height: 34px;
  border: 1px solid #c1c1cd;
  border-radius: 3px;
  cursor: pointer;
  justify-content: center;
  display: flex;
  line-height: 34px;
}

</style>