<template>
  <div>
    <h2>标签记录</h2>
    <el-button type="success" class="el-button--mini" @click="resumeBatch">批量恢复</el-button>
    <el-table
        :data="tagList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        highlight-current-row
        style="width: 100%"
        ref="selection"
        @cell-click="cellClick"
        @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55"
      >
      </el-table-column>
      <el-table-column
          sortable
          label="删除时间"
          prop="gmtModified"
          width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.gmtModified }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="分类"
          prop="name"
          width="180">
        <template slot-scope="scope">
          <el-tag size="medium" ref="name">{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column>
        <template slot="header" slot-scope="scope">
          <input
              class="search"
              v-model="search"
              size="medium"
              placeholder="输入关键字搜索"/>
        </template>
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="success"
              @click="resumeById(scope.$index, scope.row)">恢复</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>

export default {
  name: "history",
  data(){
    return{
      tagList:[],
      search:"",
      selections:[]
    }
  },
  created() {
    this.getAllTag()
  },
  methods:{
    resumeBatch(){
      this.$confirm("确定要批量恢复选中的记录?","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/tag/resumeBatch`,this.selections).then(res=>{
          this.$message.success({message:"恢复成功"})
          this.getAllTag()
        })
      })
    },
    cellClick(row){
      this.$refs.selection.toggleRowSelection(row)
    },
    selectionChange(row){
      this.selections = row
      console.log(this.selections)
    },
    //查询所有分类
    getAllTag(){
      this.getRequest(`/tora/tag/getAllDeleted`).then(response=>{
        this.tagList = response.data.records

      })
    },
    resumeById(index, row) {
      this.$confirm("是否恢复该记录？","提示",{
        confirmButtonText:"是",
        cancelButtonText:"否"
      }).then(()=>{
        this.getRequest(`/tora/tag/resumeById/${row.id}`).then(res=>{
          this.$message.success({message:"恢复成功"})
          this.search = ""
          this.getAllTag()
        })
      })
    },
    loseFcous(index, row) {
      row.seen=false;
    },
    cellDoubleClick(row, column) {
      row.seen=true;
      this.$nextTick(()=>{
        this.$refs.name.focus()
      })
    }
  }
}
</script>

<style scoped>
.tb-edit .el-input {
  display: none
}
.tb-edit .current-row .el-input {
  display: block
}
.tb-edit .current-row .el-input+span {
  display: none
}
.search{
  border-radius: 2px;
  padding: 10px 8px 10px 8px;
  border: 0 none;
  border-bottom: 1px solid #ccc;
}
.el-table-add-row {
  margin-top: 10px;
  width: 100%;
  height: 34px;
  border: 1px solid #c1c1cd;
  border-radius: 3px;
  cursor: pointer;
  justify-content: center;
  display: flex;
  line-height: 34px;
}

</style>