import axios from 'axios';
import {Message} from 'element-ui';
import router from "@/router";


//路由拦截
router.beforeEach(function (to, from, next) {
    //不需要登录的页面执行next 需要登录的页面判断是否登陆过登陆过next 没登陆过跳转登录
    if (!to.meta.noNeedLogin){
        if (window.sessionStorage.getItem("username")){
            next()
        } else {
            alert("请登录")
            next({path:"/"})
        }
    } else {
        next()
    }
    
})


//响应拦截器
axios.interceptors.response.use(success => {
    //业务逻辑错误
    if (success.status && success.status == 200) {
        /* if (success.data.code == 20001) {
            Message.error({message: success.data.message});
            return;
        }
        if (success.data.message) {
            Message.success({message: success.data.message});
        }*/
    }
    return success.data;
}, error => {
    if (error.response.code == 504 || error.response.code == 404) {
        Message.error({message: "服务器被吃了..."});
    } else if (error.response.code == 403) {
        Message.error({message: "你没有这个权限哦"});
    } else if(error.response.code==401){
        Message.error({message:"请先登录!"});
        router.replace("/");
    } /*else{
        if (error.response.data.message){
            Message.error({message:error.response.data.message});
        }else{
            Message.error({message:"未知错误!"});
        }
    }*/
    return;
})

let base = ""

//传送json格式的post请求
export const postRequest = (url,param) =>{
    return axios({
        method:"post",
        url:`${url}`,
        data:param
    })
}


export const getRequest = (url,param) =>{
    return axios({
        method:"get",
        url:`${url}`,
        data:param
    })
}


export const deleteRequest = (url,param) =>{
    return axios({
        method:"delete",
        url:`${url}`,
        data:param
    })
}