<template>
  <div>
  <el-container>
    <el-header class="homeHeader">
      <div class="title">Tora博客管理后台</div>
      <el-dropdown class="userInfo" @command="handleCommand">
        <span class="el-dropdown-link" style="margin-right: 50px">
          <i><img :src="user.avatar"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
          <el-dropdown-item command="logout">注销</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu @select="menuClick">
          <el-submenu index="1" style="background-color: #f0f8ff">
            <template slot="title"><i class="el-icon-document"></i>博客管理</template>
              <el-menu-item index="/blog">博客列表</el-menu-item>
              <el-menu-item index="/blogSave">博客编辑</el-menu-item>
              <el-menu-item index="/blogHistory">历史删除</el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-collection-tag"></i>标签管理</template>
            <el-menu-item index="/tag">标签列表</el-menu-item>
            <el-menu-item index="/tagHistory">历史删除</el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"><i class="el-icon-aim"></i>分类管理</template>
            <el-menu-item index="/classify">分类列表</el-menu-item>
            <el-menu-item index="/classifyHistory">历史删除</el-menu-item>
          </el-submenu>
          <el-submenu index="4">
            <template slot="title"><i class="el-icon-s-comment"></i>评论管理</template>
            <el-menu-item index="/comment">评论列表</el-menu-item>
            <el-menu-item index="/commentHistory">历史删除</el-menu-item>
          </el-submenu>
          <el-submenu index="5">
            <template slot="title"><i class="el-icon-user"></i>用户管理</template>
            <el-menu-item index="/user">用户列表</el-menu-item>
            <el-menu-item index="/userHistory">历史删除</el-menu-item>
          </el-submenu>
          <el-submenu index="6">
            <template slot="title"><i class="el-icon-user"></i>测试页面</template>
            <el-menu-item index="/test">测试页面</el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view />
      </el-main>
    </el-container>
  </el-container>
  </div>
</template>

<script>
export default {
  name: "home",
  data(){
    return {
      username:window.sessionStorage.getItem("username"),
      user:{},
    }
  },
  created() {
    this.getUserInfo()
  },
  methods:{
    menuClick(index){
      this.$router.push(index)
    },
    getUserInfo(){
      this.getRequest(`/tora/user/getUserInfo/${this.username}`).then(res=>{
        this.user = res.data.user
      })
    },
    handleCommand(command) {
      if (command=="logout"){
        this.$confirm("是否注销登录？","提示",{
          confirmButtonText:"注销",
          cancelButtonText:"取消",
          type:"warning"
        }).then(()=>{
          this.getRequest(`/tora/user/logout`).then(res=>{
            if (res.code==20000){
              window.sessionStorage.removeItem("username")
              this.$router.replace("/")
              this.$message.success("注销成功")
            }
          })
        })
      } else if (command=="userInfo"){
        this.$router.push({path:"userInfo"})
      }
    }
  }

}
</script>

<style scoped>
.homeHeader{
  background: #f0f8ff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
}
.homeHeader .title{
  font-size: 30px;
  font-family: 华文行楷;
  color: #43c0f5;
}
.homeHeader .userInfo{
  cursor: pointer;
}
.el-dropdown-link img{
  width: 48px;
  height: 48px;
  border-radius: 24px;
  margin-left: 8px;
}
.el-submenu {
  background-color: #f0f8ff;
}
</style>