<template>
  <div>
    <h3>评论列表</h3>

    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="commentQuery.title" placeholder="标题"/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="commentQuery.username" placeholder="用户名"/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="commentQuery.keyWord" placeholder="关键字"/>
      </el-form-item>
      <br>
      <el-form-item>
        <el-select v-model="commentQuery.status" clearable placeholder="状态">
          <el-option :value="1" label="已审核"/>
          <el-option :value="0" label="未审核"/>
          <el-option :value="-1" label="未通过"/>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-date-picker
            v-model="commentQuery.begin"
            type="date"
            placeholder="选择开始时间"
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getAllComment()">查询</el-button>
      <el-button type="default" @click="resetData()">重置</el-button>
      <el-dropdown
          trigger="click"
          style="margin-left: 10px"
          @command="handleCommand"
      >
        <el-button>
          批量操作<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown" >
          <el-dropdown-item
              icon="el-icon-check"
              command="1"
          >
            通过
          </el-dropdown-item>
          <el-dropdown-item
              icon="el-icon-close"
              command="2"
          >
            驳回
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-form>

    <el-table ref="select" :data="commentList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column label="序号" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label="博客标题" width="160" />

      <el-table-column label="状态" width="80">
        <template slot-scope="scope">{{ scope.row.tpublish == 1 ? '已审核' : scope.row.tpublish == 0 ? "未审核" : "未通过" }}</template>
      </el-table-column>

      <el-table-column prop="content" label="评论内容"></el-table-column>

      <el-table-column prop="username" label="用户名" width="100"/>

      <el-table-column prop="gmtCreate" sortable label="发布时间" width="160"/>

      <el-table-column label="操作" width="300" align="center">
        <template slot-scope="scope">
          <el-button
              type="success"
              size="mini"
              icon="el-icon-check"
              @click="adopt(scope.row)"
          >
            通过
          </el-button>
          <el-button
              type="danger"
              size="mini"
              icon="el-icon-close"
              @click="reject(scope.row)"
          >
            驳回
          </el-button>
          <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="remove(scope.row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getAllComment"
    />
  </div>
</template>

<script>
export default {
  name: "comment",
  data(){
    return{
      commentList:[],
      page:1,
      limit:15,
      total:0,
      commentQuery:{
        title:"",
        username:"",
        keyWord:"",
        status:0,
        begin:""
      },
      selections:[]
    }
  },
  created() {
    this.getAllComment()
  },
  methods:{
    remove(row){
      this.$confirm("此操作将永久删除该记录，是否继续？","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        this.deleteRequest(`/tora/comment/delete/${row.id}`).then(()=>{
          this.getAllComment(this.page)
        })
      })
    },
    handleCommand(command){
      if (command==1){
        this.$confirm("此操作将<span style='color: #6cd76c'>批量通过</span>选中记录，是否继续？","提示",{
          confirmButtonText:"确定",
          cancelButtonText:"取消",
          type:"warning",
          dangerouslyUseHTMLString: true
        }).then(()=>{
          this.postRequest(`/tora/comment/adoptBatch`,this.selections).then(()=>{
            this.getAllComment()
          })
        })
      }else{
        this.$confirm("此操作将<span style='color: red'>批量删除</span>选中记录，是否继续？","提示",{
          confirmButtonText:"确定",
          cancelButtonText:"取消",
          type:"warning",
          dangerouslyUseHTMLString: true
        }).then(()=>{

        })
      }
    },
    resetData(){
      this.getAllComment(this.page);
    },
    selectionChange(row){
      this.selections = row
      console.log(this.selections)
    },
    cellClick(row){
      this.$refs.select.toggleRowSelection(row)
    },
    adopt(row){
      this.getRequest(`/tora/comment/adopt/${row.id}`).then(()=>{
        this.getAllComment()
      })
    },
    reject(row){
      this.getRequest(`/tora/comment/reject/${row.id}`).then(()=>{
        this.getAllComment()
      })
    },
    getAllComment(page = 1){
      this.page = page
      this.postRequest(`/tora/comment/queryCommentPage/${this.page}/${this.limit}`,this.commentQuery).then(res=>{
        this.commentList = res.data.records
        this.total = res.data.total
      })
    }
  }
}
</script>

<style scoped>

</style>