<template>
  <div>
    <el-form :rules="rules" ref="loginForm" :model="loginForm" class="loginContainer">
      <h2 class="loginTitle">系统登录</h2>
      <el-form-item prop="username">
        <el-input type="text" v-model="loginForm.username" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input type="text" v-model="loginForm.code" placeholder="点击图片更换验证码" style="width: 200px;margin-right: 5px" @keyup.enter.native="loginSubmit"></el-input>
        <img :src="captchaUrl" alt="验证码" @click="updateCaptha"></img>
      </el-form-item>
      <el-form-item>
        <el-button type="success" class="loginButton" @click="loginSubmit">登录</el-button>
        <el-button type="primary" >注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "login",
  data(){
    return{
      loginForm:{
        username:"admin",
        password:"admin",
        code:""
      },
      captchaUrl:"/tora/user/getVerityCode?time="+new Date(),
      rules:{
        username: [{required:true, message:"请输入用户名", trigger:'blur'},
                   { min: 3, max: 8, message: '长度在 8 到 8npm 个字符', trigger: 'blur' }],
        password: [{required:true, message:"请输入密码", trigger:"blur"}],
        code:[{required:true, message:"请输入验证码", trigger:"blur"}]
      }
    }
  },
  created() {
    this.updateCaptha();
  },
  methods:{
    loginSubmit(){
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.postRequest("/tora/user/login",this.loginForm).then(response=>{
            if (response.code==20000){
              window.sessionStorage.setItem("username",response.data.username)
              this.$router.replace('/home');
            }else{
              this.$message.error(response.message)
              this.updateCaptha()
              return false;
            }
          })
        } else {
          this.$message.error("请输入完整信息")
          return false;
        }
      });
    },
    updateCaptha(){
      this.captchaUrl = "/tora/user/getVerityCode?time="+new Date()
    }
  }
}
</script>

<style>
  .loginContainer{
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 15px 35px 15px 35px;
    background: aliceblue;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .loginTitle{
    margin: 10px auto 30px auto;
    text-align: center;
  }
  .loginButton{
    float: right;
    margin: 0px 10px 0px 10px;
  }
  .el-form-item__content{
    display: flex;
    align-items: center;
  }
</style>