<template>
  <el-card style="margin:0 auto; width:1200px;font-family: 微软雅黑">
    <div>
      <h3>博客评论</h3>
      <div style="margin-bottom: 10px">
        <el-input type="textarea" v-model="commentForm.content" :rows="3" placeholder="请输入评论内容..." style="float: left;width: 50%;margin-right: 5px"></el-input>
        <el-button type="primary" style="width: 8%;height: 75px" @click="comment()">发表评论</el-button>
      </div>

      <br>
      <div>
        <el-form :model="replyForm" status-icon ref="replyForm">
          <el-row :gutter="10" v-for="(comment,index) in commentList" :key="index">
            <el-col :span="1.5">
              <i><img :src="comment.avatar" class="avatar"></i>
            </el-col>
            <el-col :span="6" style="line-height: 26px">
              <el-col :span="22"><strong>{{comment.username}}</strong></el-col>
              <el-col :span="22" style="color: #999999">
                {{comment.gmtCreate}}
                <span :span="6" @click="commentHandle (index)" style="cursor:pointer;margin: 0">
                  <i class="el-icon-chat-dot-square"></i>
                  <span style="color:#bab7b7;font-size: 14px">回复</span>
                </span>
              </el-col>
            </el-col>
            <el-col :span="22" style="font-size: 17px;line-height: 22px;padding-top: 8px;margin-left: 75px">
              {{comment.content}}
            </el-col>
            <el-col :span="24">
              <div style="padding:10px 0px" v-if="choice === index">
                <el-form-item>
                  <el-input
                      v-model="replyForm.content"
                      type="textarea"
                      style="width: 80%"
                      :rows="3"
                      maxlength="255"
                      show-word-limit
                      placeholder="请输入内容">
                  </el-input>
                </el-form-item>
                <div style="width: 80%;text-align: right;margin-top: 10px">
                  <el-button @click="commentBack">取消</el-button>
                  <el-button type="primary" @click="replyComment (comment.id)">发送</el-button>
                </div>
              </div>
            </el-col>
            <!--        <el-col :span="2" @click="commentHandle (index)" style="cursor:pointer;">-->
            <!--          <i class="el-icon-chat-dot-square"></i>-->
            <!--          <span style="color:#bab7b7;font-size: 14px">评论</span>-->
            <!--        </el-col>-->
            <el-col :span="24">
              <div
                  v-for="(reply,index1) in comment.children"
                  style="border-left: #c7c4c4 solid 3px;margin-left: 6px;margin-top: 8px;padding-left: 15px;font-size:16px ">
                <div style="border-bottom: #c7c4c4 dashed  1px;padding: 10px 10px">
                <span>
                  {{reply.username}}
                </span>
                  <span style="color: #3e9cfe" v-if="reply.to!=comment.username">
                  @{{reply.to}}
                </span>
                  ：
                  <span>
                  {{reply.content}}
                </span>
                  <div style="padding-top: 10px;font-size: 13px;color:#bab7b7 ">
                    {{reply.gmtCreate}}
                    <span :span="6" @click="commentHandle (index1+reply.id+index+num)" style="cursor:pointer;margin: 0">
                    <i class="el-icon-chat-dot-square"></i>
                    <span style="color:#bab7b7;font-size: 14px">回复</span>
                  </span>
                  </div>
                </div>
                <!--            <div style="padding:10px 0px">-->
                <!--              <span style="color: #bab7b7;cursor:pointer;" @click="commentHandle (index)">-->
                <!--                回复-->
                <!--              </span>-->
                <!--            </div>-->
                <div style="padding:10px 0px" v-if="choice === index1+reply.id+index+num">
                  <el-form-item>
                    <el-input
                        v-model="replyForm.content"
                        type="textarea"
                        style="width: 80%"
                        :rows="3"
                        maxlength="255"
                        show-word-limit
                        placeholder="请输入内容">
                    </el-input>
                  </el-form-item>
                  <div style="width: 80%;text-align: right;margin-top: 10px">
                    <el-button @click="commentBack">取消</el-button>
                    <el-button type="primary" @click="replyComment (reply.id)">发送</el-button>
                  </div>
                </div>
              </div>
            </el-col>


            <el-col :span="24" >
              <el-divider></el-divider>
            </el-col>

          </el-row>
        </el-form>
      </div>

      <!-- 分页 -->
      <el-pagination
          :current-page="page"
          :page-size="limit"
          :total="total"
          style="padding: 30px 0; text-align: center;"
          layout="prev, pager, next, jumper"
          @current-change="handleCurrentChange"
      />
    </div>
  </el-card>
</template>

<script>
export default {
  name: "blogComment",
  data(){
    return{
      commentList:{},
      page:1,
      limit:2,
      total:0,
      user:{},
      choice:-1,
      commentForm: {
        content: ""
      },
      replyForm:{
        content:""
      },
      blogId:this.$route.params.id,
      num:Math.round(Math.random()*10)
    }
  },
  created() {
    this.getAllComment()
    this.getUserInfo()
  },
  methods:{
    handleCurrentChange(page){
      this.page = page
      this.getAllComment(this.page)
      this.commentBack()
    },
    getUserInfo(){
      this.getRequest(`/tora/user/getUserInfo/${window.sessionStorage.getItem("username")}`).then(res=>{
        this.user = res.data.user
        console.log("this.user : " + this.user.id)
      })
    },
    replyComment(id){
      this.replyForm = {
        blogId:this.blogId,
        parentId:id,
        userId:this.user.id,
        content:this.replyForm.content
      }
      this.postRequest(`/tora/comment/addComment`,this.replyForm).then(()=>{
        this.getAllComment(this.page);
        this.replyForm.content = ""
        this.$message.success({message:"发表成功!请审核!"})
      })
    },
    comment(){
      this.commentForm = {
        blogId:this.blogId,
        parentId:0,
        userId:this.user.id,
        content:this.commentForm.content
      }
      this.postRequest(`/tora/comment/addComment`,this.commentForm).then(()=>{
        this.getAllComment(this.page);
        this.commentForm.content = ""
        this.$message.success({message:"发表成功!请审核!"})
      })
    },
    //评论框弹出
    commentHandle (index) {
      if (index != this.choice) {
        this.choice = index
        this.replyForm.content = ""
        this.$refs['replyForm'].resetFields()
        console.log("choice : " + this.choice)
        console.log("index : " + index)
      } else {
        this.commentBack()
      }
    },
    //评论框取消
    commentBack () {
      this.$refs['replyForm'].resetFields()
      this.choice = -1
      this.replyForm.content = ""
      this.num = Math.round(Math.random()*10)
    },
    getAllComment(page = 1){
      this.page = page
      this.getRequest(`/tora/comment/getCommentByBlogId/${this.$route.params.id}/${this.page}/${this.limit}`).then(res=>{
        this.commentList = res.data.records
        this.total = res.data.total
      })
    }
  }
}
</script>

<style scoped>
.avatar{
  width: 48px;
  height: 48px;
  border-radius: 24px;
  margin-left: 8px;
}
.el-divider {
  height: 0.1px;
}

.el-tab-pane /deep/ .el-card__body {
  background-color: #0b5b76 !important;
}
</style>