<template>
  <div>
    <h2>标签管理</h2>
    <el-button type="danger" class="el-button--mini" @click="removeBatch">批量删除</el-button>
    <el-table
        :data="tagList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        highlight-current-row
        style="width: 100%"
        ref="selection"
        @cell-dblclick="cellDoubleClick"
        @cell-click="cellClick"
        @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55"
      >
      </el-table-column>
      <el-table-column
          label="日期"
          prop="gmtCreate"
          width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.gmtCreate }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="标签"
          prop="name"
          width="180">
        <template slot-scope="scope">
          <el-input size="medium"
                    v-model="scope.row.name"
                    v-if="scope.row.seen"
                    placeholder="请输入内容"
                    ref="name"
                    @keyup.enter.native="addRow"
                    @blur="loseFcous(scope.$index, scope.row)"
          ></el-input>
          <el-tag size="medium" ref="name" v-else>{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column>
        <template slot="header" slot-scope="scope">
          <input
              class="search"
              v-model="search"
              size="medium"
              placeholder="输入关键字搜索"/>
        </template>
        <template slot-scope="scope">
          <el-button
              v-if="!scope.row.id"
              size="mini"
              type="success"
              @click="handleSave(scope.$index, scope.row)">
            保存
          </el-button>
          <el-button
              size="mini"
              type="danger"
              v-if="scope.row.id"
              @click="handleDelete(scope.$index, scope.row)">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-col :span=24>
      <div class="el-table-add-row" style="width: 99.2%;" @click="addRow()"><span>+ 添加</span></div>
    </el-col>
  </div>
</template>


<script>

export default {
  name: "tag",
  data(){
    return{
      tagList:[],
      search:"",
      selections:[]
    }
  },
  created() {
    this.getAllTag()
  },
  methods:{
    removeBatch(row){
      this.$confirm("确定要批量删除选中的记录?","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/tag/removeBatch`,this.selections).then(res=>{
          this.getAllTag()
        })
      })
    },
    handleSave(row){
      this.postRequest(`/tora/tag/addTag`,row)
    },
    //添加新行
    addRow(){
      let row = {name:"",seen:true}
      this.tagList.push(row)
      console.log(row)
      this.$nextTick(()=>{
        this.$refs.name.focus()
      })
    },
    cellClick(row){
      this.$refs.selection.toggleRowSelection(row)
    },
    selectionChange(row){
      this.selections = row
      console.log(this.selections)
    },
    //查询所有分类
    getAllTag(){
      this.getRequest(`/tora/tag/getAllTag`).then(response=>{
        this.tagList = response.data.tagList

      })
    },
    handleEdit(index, row) {
      if (row.name){
        this.postRequest(`/tora/tag/updateTag`,row).then(res=>{
          this.$message.success({message:"修改成功"})
        })
      }else {
        this.$message.error({message:"请输入内容"})
        this.getAllTag()
      }

    },
    handleDelete(index, row) {
      this.$confirm("是否删除该记录？","提示",{
        confirmButtonText:"删除",
        cancelButtonText:"取消"
      }).then(()=>{
        this.deleteRequest(`/tora/tag/deleteByTagId/${row.id}`).then(res=>{
          this.$message.success({message:"删除成功"})
          this.getAllTag()
        })
      })
    },
    loseFcous(index, row) {
      row.seen=false;
      if (!row.id&&!row.name){
        this.tagList.pop(row)
      }else if (!row.id&&row.name){
        this.handleSave(row)
      }else{
        this.handleEdit(index,row)
      }
    },
    cellDoubleClick(row, column) {
      row.seen=true;
      this.$nextTick(()=>{
        this.$refs.name.focus()
      })
    }
  }
}
</script>

<style scoped>
.tb-edit .el-input {
  display: none
}
.tb-edit .current-row .el-input {
  display: block
}
.tb-edit .current-row .el-input+span {
  display: none
}
.search{
  border-radius: 2px;
  padding: 10px 8px 10px 8px;
  border: 0 none;
  border-bottom: 1px solid #ccc;
}
.el-table-add-row {
  margin-top: 10px;
  width: 100%;
  height: 34px;
  border: 1px solid #c1c1cd;
  border-radius: 3px;
  cursor: pointer;
  justify-content: center;
  display: flex;
  line-height: 34px;
}

</style>