<template>
  <div>
    <h2>博客记录</h2>
    <!--查询表单-->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="blogQuery.title" placeholder="请输入标题"></el-input>
      </el-form-item>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.begin"
            type="date"
            placeholder="选择删除时间"
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getList()">查询</el-button>
      <el-button type="default" @click="resetData()">重置</el-button>
      <el-button type="success" @click="resumeBatch()">批量恢复</el-button>
    </el-form>

    <!-- 表格 -->
    <el-table ref="selection" :data="blogList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column label="序号" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label="标题"/>

      <el-table-column label="状态" width="80">
        <template slot-scope="scope">{{ scope.row.status === 1 ? '已发布' : '未发布' }}</template>
      </el-table-column>

      <el-table-column prop="username" label="用户名" width="120"/>

      <el-table-column prop="gmtCreate" label="发布时间" width="160"/>
      <el-table-column prop="gmtModified" label="删除时间" width="160"/>

      <el-table-column label="操作" width="200" align="center">
        <template slot-scope="scope">
          <el-button
              type="success"
              size="mini"
              icon="el-icon-check"
              @click="removeDataById(scope.row.id)"
          >
            恢复
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getList"
    />
  </div>
</template>

<script>
export default {
  name: "blog",
  data(){
    return{
      blogList:null,
      total:0,
      page:1,
      limit:5,
      blogQuery:{
        title:"",
        begin:""
      },
      selections:[]
    }
  },
  created() {
    this.getList()
  },
  methods:{
    resumeBatch(){
      this.$confirm("确定要批量恢复选中的记录吗","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(res=>{
        this.postRequest(`/tora/blog/resumeBatch`,this.selections).then(response=>{
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        })
      })
    },
    selectionChange(val) {
      this.selections = val
    },
    cellClick(row){
      this.$refs.selection.toggleRowSelection(row)
    },
    getList(page = 1){
      this.page = page
      this.postRequest(`/tora/blog/queryHistoryPage/${this.page}/${this.limit}`,this.blogQuery).then(res=>{
        this.blogList = res.data.records
        this.total = res.data.total
      }).catch(res=>{
        this.blogQuery={
          title:"",
          classify:"",
          tagId:"",
          username:"",
          type:"",
          begin:"",
          end:""
        }
      })
    },
    //清空查询
    resetData() {
      this.blogQuery = {
        title:"",
        classify:"",
        tagId:"",
        username:"",
        type:"",
        begin:"",
        end:""
      };
      this.getList();
    },
    //删除
    removeDataById(id) {
      //删除提示框
      this.$confirm("此操作将永久删除该记录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.getRequest(`/tora/blog/resumeById/${id}`).then(response => {
          //刷新
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          }else{
            this.getList(this.page);
          }
        });
      });
    }
  }
}
</script>

