import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../views/login.vue'
import home from "@/views/home";
import blog from "@/views/blog/blog";
import blogSave from "@/views/blog/save";
import blogHistory from "@/views/blog/history";
import classify from "@/views/classify/classify";
import classifySave from "@/views/classify/save";
import classifyHistory from "@/views/classify/history";
import comment from "@/views/comment/comment";
import commentHistory from "@/views/comment/history";
import tag from "@/views/tag/tag";
import tagSave from "@/views/tag/save";
import tagHistory from "@/views/tag/history";
import userHistory from "@/views/user/history";
import test from "@/views/test";
import blogComment from "@/views/comment/blogComment";
import userInfo from "@/views/user/userInfo";



import user from "@/views/user/user";

Vue.use(VueRouter)
//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const routes = [
  {
    path: '/',
    name: 'login',
    component: login,
    hidden:true,
    meta:{
      title:"登录",
      noNeedLogin:true  //不需要登录标识
    }
  },
  {
    path: '/home',
    name: '博客管理',
    component: home,
    children:[
      {
        path: '/blog',
        name: '博客列表',
        component: blog
      },
      {
        path: '/blogSave',
        name: '编辑博客',
        component: blogSave
      },
      {
        path: '/blogEdit/:id',
        name: '编辑博客',
        component: blogSave
      },
      {
        path: '/blogHistory',
        name: '博客历史删除',
        component: blogHistory
      },
    ]
  },
  {
    path: '/home',
    name: '分类管理',
    component: home,
    children:[
      {
        path: '/classify',
        name: '分类列表',
        component: classify
      },
      {
        path: '/classifySave',
        name: '编辑分类',
        component: classifySave
      },
      {
        path: '/classifyHistory',
        name: '分类历史删除',
        component: classifyHistory
      }
    ]
  },
  {
    path: '/home',
    name: '标签管理',
    component: home,
    children:[
      {
        path: '/tag',
        name: '标签列表',
        component: tag
      },
      {
        path: '/tagSave',
        name: '编辑标签',
        component: tagSave
      },
      {
        path: '/tagHistory',
        name: '标签历史删除',
        component: tagHistory
      },
    ]
  },
  {
    path: '/home',
    name: '评论管理',
    component: home,
    children:[
      {
        path: '/comment',
        name: '评论列表',
        component: comment
      },
      {
        path: '/commentHistory',
        name: '评论历史删除',
        component: commentHistory
      },
      {
        path: '/blogComment/:id',
        name: '博客评论',
        component: blogComment
      }
    ]
  },
  {
    path: '/home',
    name: '用户管理',
    component: home,
    children: [
      {
        path: '/user',
        name: '用户列表',
        component: user
      },
      {
        path: '/userHistory',
        name: '用户历史删除',
        component: userHistory
      },
      {
        path: '/userInfo',
        name: '个人中心',
        component: userInfo
      }
    ]
  },
  {
    path: '/home',
    name: '测试页面',
    component: home,
    children: [
      {
        path: '/test',
        name: '测试',
        component: test
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
