<template>
  <div>
    <el-card class="box-card"  style="margin:0 auto; width:400px;font-family: 微软雅黑">
      <div slot="header" class="clearfix">
        <span>{{ user.username }}</span>
      </div>
      <div>
        <div style="display: flex;justify-content: center;margin-bottom: 15px">
          <img title="点击修改头像" :src="user.avatar" alt="头像" style="width: 100px;height: 100px;border-radius: 50px">
        </div>
        <div style="display: flex;justify-content: center;margin-bottom: 5px"><el-tag type="success">{{user.email}}</el-tag></div>
        <div style="display: flex;justify-content: center"><el-tag type="success">{{user.isAdmin==1 ? '管理员' : user.isAdmin==0 ? '注册用户' : '游客'}}</el-tag></div>
        <div style="display: flex;justify-content: space-around;margin-top: 15px">
          <el-button type="primary">修改信息</el-button>
          <el-button type="danger" @click="showPwdDialog">修改密码</el-button>
        </div>
      </div>
    </el-card>
    <el-dialog
        title="修改密码"
        :visible.sync="pwdDialogVisible"
        width="30%"
        :before-close="closeDialog">
      <div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="旧密码" prop="oldPass">
            <el-input type="password" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "userInfo",
  data(){
    var validatePass = (rule, value, callback) => {
      if (value == '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return{
      username:window.sessionStorage.getItem("username"),
      user:{},
      pwdDialogVisible:false,
      ruleForm: {
        id:"",
        pass: '',
        checkPass: '',
        oldPass: ''
      },
      rules: {
        oldPass:[{ required: true, message: '请输入旧密码', trigger: 'blur'}],
        pass: [
          { required: true, validator: validatePass, trigger: 'blur' },{ min: 5, max: 20, message: '长度在 5 到 20 以内', trigger: 'blur'}
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: 'blur' }
        ]
      }
    };
  },
  created() {
    this.getUserInfo()
  },
  methods:{
    showPwdDialog(){
      this.pwdDialogVisible = true
      this.ruleForm = {
        id : this.user.id,
        pass: '',
        checkPass: '',
        oldPass: ''
      }
    },
    submitForm(ruleForm) {
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          this.postRequest(`/tora/user/changePassword`,this.ruleForm).then(res=>{
            if (res.code==20000){
              this.$message.success("修改成功!请重新登录")
              this.$router.replace("/")
            }else{
              this.$message.error(res.message)
            }
          })
        } else {
          return false;
        }
      });
    },
    resetForm(ruleForm) {
      this.$refs[ruleForm].resetFields();
    },
    closeDialog(){
      this.pwdDialogVisible = false
    },
    getUserInfo(){
      this.getRequest(`/tora/user/getUserInfo/${this.username}`).then(res=>{
        this.user = res.data.user
      })
    }
  }
}
</script>

