<template>
  <div>
    <h2>博客管理</h2>
    <!--查询表单-->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="blogQuery.title" placeholder="标题"/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="blogQuery.username" placeholder="用户名"/>
      </el-form-item>

      <el-form-item>
        <el-select v-model="blogQuery.status" clearable placeholder="状态">
          <el-option :value="1" label="已发布"/>
          <el-option :value="0" label="未发布"/>
        </el-select>
      </el-form-item>
      <br>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.begin"
            type="date"
            placeholder="选择开始时间"
            value-format="yyyy-MM-dd"
        />
      </el-form-item>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.end"
            type="date"
            placeholder="选择截止时间"
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getList()">查询</el-button>
      <el-button type="default" @click="resetData()">重置</el-button>
      <el-button type="danger" @click="removeBatch">批量删除</el-button>
    </el-form>

    <!-- 表格 -->
    <el-table ref="select" :data="blogList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column label="序号" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label="标题" />

      <el-table-column label="状态" width="80">
        <template slot-scope="scope">{{ scope.row.tpublish ? '已发布' : '未发布' }}</template>
      </el-table-column>

      <el-table-column prop="username" label="用户名" width="100"/>

      <el-table-column prop="gmtCreate" sortable label="发布时间" width="160"/>
      <el-table-column prop="gmtModified" sortable label="最后更新时间" width="160"/>

      <el-table-column label="操作" width="300" align="center">
        <template slot-scope="scope">
          <router-link :to="'/blogComment/'+scope.row.id">
            <el-button type="primary" size="mini" icon="el-icon-chat-line-round" style="margin-right: 10px">查看评论</el-button>
          </router-link>
          <router-link :to="'/blogEdit/'+scope.row.id">
            <el-button type="primary" size="mini" icon="el-icon-edit" style="margin-right: 10px">编辑</el-button>
          </router-link>
          <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="removeDataById(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getList"
    />
  </div>
</template>

<script>
export default {
  name: "blog",
  data(){
    return{
      blogList:null,
      total:0,
      page:1,
      limit:8,
      blogQuery:{
        title:"",
        classify:"",
        tagId:"",
        status:null,
        username:"",
        type:"",
        begin:"",
        end:""
      },
      selections:[],
    }
  },
  created() {
    this.getList()
  },
  methods:{
    removeBatch(){
      this.$confirm("确定要批量删除选中的记录？","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/blog/deleteBatch`,this.selections).then(response=>{
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        })
      })
    },
    selectionChange(val){
      this.selections = val
      console.log(this.selections)
    },
    cellClick(row){
      this.$refs.select.toggleRowSelection(row)
    },
    getList(page = 1){
      this.page = page
      this.postRequest(`/tora/blog/queryBlogPage/${this.page}/${this.limit}`,this.blogQuery).then(res=>{
        this.blogList = res.data.records
        this.total = res.data.total
      }).catch(res=>{
        this.blogQuery={
          title:"",
          classify:"",
          tagId:"",
          username:"",
          type:"",
          begin:"",
          end:""
        };
      })
    },
    //清空查询
    resetData() {
      this.blogQuery = {
        title:"",
        classify:"",
        tagId:"",
        username:"",
        type:"",
        begin:"",
        end:""
      };
      this.getList();
    },
    //删除
    removeDataById(id) {
      //删除提示框
      this.$confirm("此操作将永久删除该记录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.deleteRequest(`/tora/blog/deleteBlogById/${id}`).then(response => {
          //刷新
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        });
      });
    }
  }
}
</script>

